import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';

import { withRouter } from 'next/router';

const activeCSS = css`
	content: '';
	position: absolute;
	width: 100%;
	height: 0.1em;
	bottom: -5px;
	left: 0;
	background: -webkit-linear-gradient(right, red, #008cff, #50ff00);
	background: linear-gradient(270deg, red, #008cff, #50ff00);
	background-size: 600% 600%;
	-webkit-animation: _2S0HYUuRGSgZN7w7T0tH4p 30s ease infinite;
	animation: _2S0HYUuRGSgZN7w7T0tH4p 30s ease infinite;
	-webkit-transform: scaleX(0);
	transform: scaleX(0);
	-webkit-transition: all 0.48s cubic-bezier(1, 0, 0, 1);
	transition: all 0.48s cubic-bezier(1, 0, 0, 1);
	-webkit-transform-origin: left;
	transform-origin: left;
`;

const Link = styled.a`
	&:before {
		${activeCSS}
	}
`;

const Item = ({ children, router, href }) => {
	const handleClick = e => {
		e.preventDefault();
		router.push(href);
	};

	return (
		<Link href={href} onClick={handleClick} active={router.pathname === href}>
			{children}
		</Link>
	);
};

Item.propTypes = {
	href: PropTypes.string.isRequired,
	router: PropTypes.object.isRequired,
	children: PropTypes.node.isRequired,
};

export default withRouter(Item);
